import {injectable} from 'inversify'
import type {Dream} from '../domain/Dream'

export interface DreamRepository {
  registerMessage(dream: Dream): void

  getCurrentMessages(limit: number): Dream[]
}


@injectable()
export class LocalDreamRepositoryImpl implements DreamRepository {

  private _dreams: Dream[] = []

  registerMessage(dream: Dream): void {
    this._dreams.push(dream)
  }

  getCurrentMessages(limit: number): Dream[] {
    return this._dreams.slice(-limit)
  }
}

let DREAM_REPOSITORY_TYPES = {
  DreamRepository: Symbol('DreamRepository')
}

export default DREAM_REPOSITORY_TYPES
