import {inject, injectable} from 'inversify'
import type {Dream} from '../domain/Dream'
import DREAM_REPOSITORY_TYPES, * as DreamRepository from '../repository/DreamRepository'

export interface DreamService {
  postDream(dream: Dream): void

  getCurrentDreams(limit: number): Dream[]
}


@injectable()
export class DreamServiceImpl implements DreamService {
  constructor(
    @inject(DREAM_REPOSITORY_TYPES.DreamRepository) private _repository: DreamRepository.DreamRepository
  ) {}

  postDream(dream: Dream): void {
    this._repository.registerMessage(dream)
  }

  getCurrentDreams(limit: number): Dream[] {
    return this._repository.getCurrentMessages(limit)
  }
}

let DREAM_SERVICE_TYPES = {
  DreamService: Symbol('DreamService')
}

export default DREAM_SERVICE_TYPES
