import {Container} from 'inversify'
import 'reflect-metadata'
import DREAM_REPOSITORY_TYPES, {DreamRepository, LocalDreamRepositoryImpl} from '../application/repository/DreamRepository'
import DREAM_SERVICE_TYPES, {DreamService, DreamServiceImpl} from '../application/service/DreamService'

export const container = new Container()

container.bind<DreamRepository>(DREAM_REPOSITORY_TYPES.DreamRepository).to(LocalDreamRepositoryImpl).inSingletonScope()
container.bind<DreamService>(DREAM_SERVICE_TYPES.DreamService).to(DreamServiceImpl).inSingletonScope()
